<script>
import Plyr from "plyr";
import "plyr/dist/plyr.css";
export default {
  data() {
    return {
      clickedAnswer: null,
      player: null,
    };
  },
  methods: {
    toggleAnswer(e, event) {
      if (event) {
        let videoContainer = document.getElementById("video-box");

        if (videoContainer && videoContainer.contains(event.target)) {
          return;
        }
      }

      if (this.clickedAnswer === e) {
        this.clickedAnswer = null;
      } else {
        this.clickedAnswer = e;

        setTimeout(() => {
          //  window.scrollTo(0, document.getElementById(e).offsetTop);

          window.scrollTo({
            top: document.getElementById(e).offsetTop,

            behavior: "smooth",
          });
        }, 300);
      }
    },
    enter(element) {
      const width = getComputedStyle(element).width;

      element.style.width = width;
      element.style.position = "absolute";
      element.style.visibility = "hidden";
      element.style.height = "auto";

      const height = getComputedStyle(element).height;

      element.style.width = null;
      element.style.position = null;
      element.style.visibility = null;
      element.style.height = 0;

      // Force repaint to make sure the
      // animation is triggered correctly.
      getComputedStyle(element).height;

      // Trigger the animation.
      // We use `requestAnimationFrame` because we need
      // to make sure the browser has finished
      // painting after setting the `height`
      // to `0` in the line above.
      requestAnimationFrame(() => {
        element.style.height = height;
      });
    },
    afterEnter(element) {
      element.style.height = "auto";

      if (
        this.clickedAnswer === "invocation" ||
        this.clickedAnswer === "brunch"
      ) {
        this.loadPlayer();
      }
    },
    leave(element) {
      const height = getComputedStyle(element).height;

      element.style.height = height;

      // Force repaint to make sure the
      // animation is triggered correctly.
      getComputedStyle(element).height;

      requestAnimationFrame(() => {
        element.style.height = 0;
      });
    },
    loadPlayer() {
      this.player = new Plyr("#player", {
        controls: [
          "play-large",
          "play",
          "progress",
          "current-time",
          "mute",
          "volume",
          "fullscreen",
        ],
        fullscreen: { enabled: true },
      });
      window.player = this.player;

      let audioBox = document.getElementById("audio");

      this.player.on("playing", () => {
        audioBox.pause();
      });
      audioBox.addEventListener("playing", () => {
        this.player.pause();
      });
      const videoWrapper = document.getElementsByClassName(
        "plyr__video-wrapper"
      )[0];
      videoWrapper.addEventListener("click", (event) => {
        this.player.togglePlay();
        event.stopPropagation(); // Necessary or the video will toggle twice => no playback
      });

      this.player.toggleControls(false);
    },
  },
  unmounted() {
    if (this.player) {
      this.player.destroy();
    }
  },
};
</script>
