<template>
  <div id="biographies-slide">
    <!-- Title -->

    <div class="container position-absolute">
      <div class="col-12 pt-5 title mb-5 mb-md-0 p-0">Biographies</div>
    </div>

    <div
      class="
        container-fluid
        p-0
        m-0
        bio-wrapper
        d-flex
        flex-row
        align-content-stretch align-items-start align-items-stretch
      "
    >
      <!-- Desktop -->
      <!-- Information -->

      <div
        class="
          d-none
          bio-card
          col-auto
          fade-in
          row
          g-0
          p-0
          d-md-flex
          flex-row flex-nowrap
          align-content-stretch align-items-start align-items-stretch
        "
        :class="[`bio-card-${index}`]"
        v-for="(artist, index) in contributors"
        :key="index"
        @click="toggleAnswer(artist, _, index, 'desktop')"
      >
        <div class="col-4 row bio-card-preview align-self-start m-0 p-0">
          <div class="col-12 name pb-2 d-flex align-items-end">
            {{ artist.primary.name[0].text }}
          </div>
          <div
            :style="[
              clickedAnswer === artist ? { opacity: '0', height: '0' } : '',
            ]"
            class="col-12 text"
          >
            <prismic-rich-text :field="artist.primary.preview_text" />
          </div>
          <div class="col-12 continue mb-5 pb-5">
            <span v-if="clickedAnswer === artist">Close</span>
            <span v-else
              >{{ $t("furniture.continue") }}
              <img class="con-arrow" src="@/assets/img/icons/arrow.svg" alt=""
            /></span>
          </div>
        </div>
        <div
          :class="
            clickedAnswer === artist ? 'bio-card-full' : 'bio-card-collapsed'
          "
          class="bio-card-extended align-self-stretch"
        >
          <div
            class="extended-wrapper d-flex justify-content-center row mx-auto"
          >
            <div class="col-12 text pt-4 mb-4 pb-4">
              <prismic-rich-text :field="artist.primary.text" />
            </div>
            <div
              v-for="(image, index) in artist.items"
              :key="index"
              class="col-10 pb-5 mb-4"
            >
              <img
                @click="openGallery(artist.items, index)"
                class="img-fluid"
                :src="image.image.url"
                alt=""
              />
            </div>
          </div>
        </div>
        <!-- Modal overlay -->
        <transition name="route">
          <div v-if="galleryOpen" class="modal-wrapper">
            <div
              class="
                container-fluid
                d-flex
                justify-content-center
                align-items-center
                image-outer-wrapper
              "
            >
              <img
                @click="cycleImages"
                class="img-fluid image-wrapper"
                :src="largeImage.image.url"
                alt=""
              />
            </div>

            <div @click="closeGallery" class="close-btn">Close</div>
          </div>
        </transition>
      </div>
      <!-- Mobile -->
      <!-- Information -->

      <div
        class="
          d-md-none
          bio-card
          col-auto
          fade-in
          row
          g-0
          p-0
          d-flex
          flex-row flex-nowrap
          align-content-stretch align-items-start align-items-stretch
        "
        :class="[`bio-card-mobile-${index}`]"
        v-for="(artist, index) in contributors"
        :key="index"
        :style="clickedAnswer === artist ? { background: '#ac4942' } : ''"
        @click="toggleAnswer(artist, $event, index, 'mobile')"
      >
        <div class="col-4 row bio-card-preview align-self-start m-0 p-0">
          <div class="col-12 name pb-2 d-flex align-items-end">
            {{ artist.primary.name[0].text }}
          </div>
          <div v-show="clickedAnswer === artist" class="col-12 text">
            <prismic-rich-text :field="artist.primary.text" />
         
            <swiper
         
              :class="['swiper-' + index]"
              class="pt-5 mt-5"
              :cssMode="false"
              :loop="true"
              :pagination="{
                el: `.custom-pagination${index}`,
                type: 'fraction',
              }"
              :navigation="{ nextEl: '.swiper' }"
            >
              <swiper-slide v-for="(image, index) in artist.items" :key="index">
                <img
                  class="img-fluid"
                  :width="image.image.dimensions.widsth"
                  :height="image.image.dimensions.height"
                  :src="image.image.url"
                  alt=""
                />
              </swiper-slide>
            </swiper>
    
            <div
              :class="'custom-pagination' + index"
              class="custom-pagination col-12 text-center"
            ></div>
          </div>
          <div v-if="clickedAnswer !== artist" class="col-12 text">
            <prismic-rich-text :field="artist.primary.preview_text" />
          </div>
          <div class="col-12 continue mb-5 pb-5">
            <span v-if="clickedAnswer === artist"></span>
            <span v-else
              >{{ $t("furniture.continue") }}
              <img class="con-arrow" src="@/assets/img/icons/arrow.svg" alt=""
            /></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import SwiperCore, { Navigation, Pagination } from "swiper";
SwiperCore.use([Navigation, Pagination]);

import "swiper/css";
import "swiper/css/pagination";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      locales: {
        it: "it-it",
        en: "en-gb",
      },
      contributors: [],
      clickedAnswer: null,
      largeImage: null,
      galleryOpen: false,
      gallery: [],
      currentIndex: null,
      length: null,
    };
  },
  methods: {
    getContent(locale) {
      let currentLocale = this.locales[locale];
      this.$prismic.client
        .get({
          predicates: [
            this.$prismic.predicate.any("document.type", ["biographies"]),
          ],
          lang: currentLocale,
        })
        .then((data) => {
          this.contributors = data.results[0].data.body;

          this.$nextTick(() => {
            this.observe();
          });
          this.observe();
        });
    },
    observe() {
      const faders = document.querySelectorAll(".fade-in");

      const appearOnScroll = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (!entry.isIntersecting) {
              entry.target.classList.remove("appear");
              return;
            } else {
              entry.target.classList.add("appear");
            }
          });
        },
        {
          rootMargin: `0px -${faders[0].offsetWidth}px 0px 0px`,
        }
      );
      faders.forEach((fader) => {
        appearOnScroll.observe(fader);
      });
    },
    toggleAnswer(e, event, index, type) {
      if (event) {
        let swiperContainer = document.querySelector(`.swiper-${index}`);

        if (swiperContainer && swiperContainer.contains(event.target)) {
          return;
        }
      }
      if (this.clickedAnswer === e) {
        this.clickedAnswer = null;
      } else {
        this.clickedAnswer = e;

        for (var x = 0; x < 9; x++)
          window.setTimeout(() => {
            document.getElementById("nav").style.top = "-6rem";
          }, 100 * x);

        const getOffset = (el) => {
          const rect = el.getBoundingClientRect();
          return {
            left: rect.left + window.scrollX,
            top: rect.top + window.scrollY,
          };
        };

        setTimeout(() => {
          let bioCard = null;
          if (type === "desktop") {
            bioCard = document.querySelector(`.bio-card-${index}`);
          } else {
            bioCard = document.querySelector(`.bio-card-mobile-${index}`);
          }

          bioCard.scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "center",
          });

          setTimeout(() => {
            this.$nextTick(() => {
              window.scrollTo({
                top:
                  getOffset(document.getElementById("biographies-slide")).top +
                  1,
                left: 0,
                behavior: "smooth",
              });
            });
          }, 2000);
        }, 200);
      }
    },
    openGallery(images, index) {
      document.body.style.overflow = "hidden";
      this.gallery = images;
      this.largeImage = images[index];
      this.currentIndex = index;
      this.galleryOpen = true;
      this.length = this.gallery.length;
    },
    closeGallery() {
      document.body.style.overflow = "auto";
      this.galleryOpen = false;
    },
    cycleImages() {
      if (this.currentIndex === this.length - 1) {
        this.currentIndex = 0;
        this.largeImage = this.gallery[this.currentIndex];
      } else {
        this.currentIndex++;
        this.largeImage = this.gallery[this.currentIndex];
      }
    },
  },
  created() {
    this.getContent(this.$i18n.locale);
  },

  watch: {
    $route() {
      this.getContent(this.$i18n.locale);
    },
  },
};
</script>

